<template>
  <div>
    <el-select
      :model-value="modelValue"
      @update:modelValue="fireUpdateModelValue"
      placeholder="请选择"
    >
      <el-option
        v-for="item in options"
        :key="item.id"
        :label="computeLabel(item)"
        :value="item.type"
      ></el-option>

      <el-option label="任何类型" value="*"></el-option>
    </el-select>

    <el-button type="default" title="刷新" @click="refresh">
      <el-icon> <Refresh /> </el-icon>
    </el-button>
  </div>
</template>
    
<script setup>
import { Refresh } from "@element-plus/icons-vue";
</script>
    
<script>
export default {
  name: "settings-content-types-for-project-selector",

  computed: {
    options() {
      return this.$store.getters["content_type/project_type_list"];
    },
  },

  data() {
    return {};
  },

  methods: {
    refresh() {
      this.$store.dispatch("content_type/fetch");
    },

    computeLabel(item) {
      let s1 = item.label;
      let s2 = item.patterns;
      return s1 + " (" + s2 + ")";
    },

    fireUpdateModelValue(value) {
      this.$emit("update:modelValue", value);
    },
  },

  props: {
    modelValue: String,
  },
};
</script>
