<template>
  <el-dialog title="宏变量名单">
    <el-table
      :data="items"
      max-height="320"
      border
      @current-change="handleSelect"
    >
      <el-table-column prop="name" label="变量名"></el-table-column>
      <el-table-column prop="description" label="说明"></el-table-column>
    </el-table>

    <el-input class="output-text-edit" v-model="output" readonly>
      <template #prepend> <el-button> 引用表达式 </el-button> </template>
      <template #append>
        <el-button @click="copy">
          <el-icon> <CopyDocument /> </el-icon> 复制文本
        </el-button>
      </template>
    </el-input>
  </el-dialog>
</template>

<script setup>
import { CopyDocument } from "@element-plus/icons-vue";
</script>

<script>
import { ElMessage } from "element-plus";

export default {
  name: "settings-intent-templates-macro-list-dialog",

  computed: {
    items() {
      return this.$store.getters["intent_template/macro_properties"];
    },
  },

  data() {
    return {
      output: "",
    };
  },

  methods: {
    handleSelect(item) {
      this.output = "${" + item.name + "}";
    },

    fetch() {
      this.$store.dispatch("intent_template/fetch_macro_props").then(() => {
        // this.close();
        // this.displayMacroListDialog = true;
      });
    },

    copy() {
      let text = this.output;
      this.$store.dispatch("clipboard/copy", text).then(() => {
        this.close();
      });
    },

    close() {
      this.$emit("update:modelValue", false);
    },
  },

  mounted() {
    this.fetch();
  },
};
</script>

<style scoped>
.output-text-edit {
  margin-top: 30px;
}
</style>
