<template>
  <el-dialog :title="dialogTitle" @open="open">
    <el-form label-width="120px">
      <el-form-item label="">
        <ImageSelect
          v-model="item.icon"
          image-height="100px"
          image-width="100px"
        />
      </el-form-item>

      <el-form-item label="名称">
        <el-input v-model="item.name"></el-input>
      </el-form-item>

      <el-form-item label="标题">
        <el-input v-model="item.title"></el-input>
      </el-form-item>

      <el-form-item label="分组">
        <el-input v-model="item.group"></el-input>
      </el-form-item>

      <el-form-item label="动作">
        <el-input v-model="item.method">
          <template #append>
            <el-button @click="item.method = valueDefault.method">
              使用默认值
            </el-button>
          </template>
        </el-input>
      </el-form-item>

      <el-form-item label="应用名称">
        <ExecutableUrnSelector v-model="item.with" />
      </el-form-item>

      <el-form-item label="目标类型">
        <el-select
          v-model="item.target"
          placeholder="选择目标类型"
          @change="handleTargetChange"
        >
          <el-option
            v-for="item in target_types"
            :key="item.id"
            :value="item.value"
            :label="item.label"
          >
            {{ item.label }}
          </el-option>
        </el-select>

        <div class="select-sub-type" v-show="displaySelectTypeForProject">
          <span> 项目类型：</span>
          <ProjectTypeSelector
            v-model="item.type"
            style="display: inline-block"
          />
        </div>

        <div class="select-sub-type" v-show="displaySelectTypeForFile">
          <span> 文件类型：</span>
          <FileTypeSelector v-model="item.type" style="display: inline-block" />
        </div>
      </el-form-item>

      <el-form-item label="命令">
        <el-input v-model="item.command">
          <template #append>
            <el-button @click="item.command = valueDefault.command">
              使用默认值
            </el-button>
          </template>
        </el-input>
      </el-form-item>

      <el-form-item label="参数">
        <el-input v-model="item.args" type="textarea"></el-input>
      </el-form-item>

      <el-form-item label="环境变量">
        <el-input v-model="item.env" type="textarea"></el-input>
      </el-form-item>

      <el-form-item label="工作目录">
        <el-input v-model="item.wd">
          <template #append>
            <el-button @click="item.wd = valueDefault.wd">
              使用默认值
            </el-button>
          </template>
        </el-input>
      </el-form-item>

      <el-form-item label="描述">
        <el-input v-model="item.description" type="textarea"></el-input>
      </el-form-item>
    </el-form>

    <div class="dialog-button-bar">
      <el-button type="default" @click="showMacroProps"> 宏变量... </el-button>
      <span class="spring"> </span>
      <el-button type="success" @click="save"> 保存 </el-button>
      <el-button type="default" @click="close"> 取消 </el-button>
    </div>

    <MyMacroListDialog v-model="displayMacroListDialog" />
  </el-dialog>
</template>

<script>
import GetterJS from "@/js/getter.js";
import MyMacroListDialog from "./macro-list-dialog.vue";
import FileTypeSelector from "@/components/settings/content-types/file-type-selector.vue";
import ProjectTypeSelector from "@/components/settings/content-types/project-type-selector.vue";
import ExecutableUrnSelector from "@/components/executable/exe-urn-selector.vue";
import ImageSelect from "@/components/media/image-select.vue";

const def_target_types = [
  {
    id: 1,
    label: "git 仓库",
    value: "repository",
    wd_default: "${repository.wkdir}",
  },
  {
    id: 2,
    label: "git 工作区",
    value: "worktree",
    wd_default: "${worktree.wkdir}",
  },
  {
    id: 3,
    label: "git 子模块",
    value: "submodule",
    wd_default: "${submodule.wkdir}",
  },
  {
    id: 4,
    label: "文件夹",
    value: "folder",
    wd_default: "${folder.path}",
  },
  {
    id: 5,
    label: "文件",
    value: "file",
    wd_default: "${file.path}",
  },
  {
    id: 6,
    label: "工程项目",
    value: "project",
    wd_default: "${project.fullpath}",
  },
  {
    id: 99,
    label: "任何类型",
    value: "*",
    wd_default: "${executable.path}",
  },
];

export default {
  name: "settings-intent-template-edit-dialog",

  components: {
    MyMacroListDialog,
    ProjectTypeSelector,
    FileTypeSelector,
    ExecutableUrnSelector,
    ImageSelect,
  },

  computed: {
    dialogTitle() {
      if (this.isPost()) {
        return "新建命令模板";
      }
      return "修改命令模板";
    },

    displaySelectTypeForFile() {
      let t = GetterJS.Get(this.item, "target", "");
      return t == "file";
    },

    displaySelectTypeForProject() {
      let t = GetterJS.Get(this.item, "target", "");
      return t == "project";
    },
  },

  data() {
    return {
      item: {},
      displayMacroListDialog: false,
      target_types: def_target_types,
      project_types: [],
      content_types: [],

      valueDefault: {
        wd: "${repository.wkdir}",
        command: "${executable.path}",
        method: "open",
      },

      target_type_selection: "",
    };
  },

  methods: {
    isPost() {
      let text = "" + this.method;
      return text.toLowerCase() == "post";
    },

    isPut() {
      let text = "" + this.method;
      return text.toLowerCase() == "put";
    },

    save() {
      this.prepareSave();
      let action = "";
      if (this.isPost()) {
        action = "intent_template/insert";
      } else if (this.isPut()) {
        action = "intent_template/update";
      } else {
        console.warn("bad prop.method.value: " + this.method);
        return;
      }
      let item = this.item;
      this.$store.dispatch(action, { item }).then(() => {
        this.close();
      });
    },

    close() {
      this.$emit("update:modelValue");
    },

    open() {
      this.prepareLoad();
    },

    prepareLoad() {
      let item = this.target;
      if (item == null) {
        item = {};
      }
      let str = JSON.stringify(item);
      item = JSON.parse(str);
      this.item = item;
    },

    prepareSave() {
      let item = GetterJS.Get(this, "item", {});
      this.item = item;
    },

    handleTargetChange(value) {
      let all = def_target_types;
      for (var i in all) {
        let item = all[i];
        if (item.value == value) {
          this.valueDefault.wd = item.wd_default;
        }
      }
    },

    showMacroProps() {
      let e = { name: "list:macro" };
      this.$emit("action", e);
    },
  },

  props: {
    method: String, // ['POST'|'PUT']
    target: Object,
  },
};
</script>

<style scoped>
.select-sub-type {
  margin-left: 20px;
}

.dialog-button-bar {
  display: flex;
  flex-direction: row;
}

.spring {
  flex-grow: 1;
}
</style>
