<template>
  <div>
    <el-table :data="items" @selection-change="handleSelectionChange">
      <el-table-column type="selection"></el-table-column>

      <el-table-column width="48px">
        <template #default="scope">
          <ImageView
            v-model="scope.row.icon"
            image-height="24px"
            image-width="24px"
          />
        </template>
      </el-table-column>

      <el-table-column prop="name" label="名称" sortable></el-table-column>
      <el-table-column prop="title" label="标题" sortable></el-table-column>
      <el-table-column prop="group" label="分组" sortable></el-table-column>

      <!-- <el-table-column prop="selector" label="选择器"></el-table-column> -->
      <!-- <el-table-column prop="state" label="状态"></el-table-column> -->

      <el-table-column prop="name" label="操作" width="170px">
        <template #default="scope">
          <el-button type="primary" link> 详细 </el-button>

          <el-button
            type="primary"
            link
            @click="handleClickItemEdit(scope.row)"
          >
            编辑
          </el-button>

          <el-button
            type="danger"
            link
            @click="handleClickItemRemove(scope.row)"
          >
            删除
          </el-button>
        </template>
      </el-table-column>
    </el-table>

    <MyBatch v-model="selectedItems" />

    <!-- <pre>  debug-info: {{ debugJson }} </pre> -->

    <MyEditDialog
      v-model="displayEditDialog"
      method="PUT"
      :target="theEditDialogItem"
      @action="handleAction"
    />
  </div>
</template>

<script>
import { ElMessageBox } from "element-plus";
import MyEditDialog from "./edit-dialog.vue";
import ImageView from "@/components/media/image-view.vue";
import MyBatch from "./batch.vue";

export default {
  name: "settings-intent-templates-table",

  components: { MyEditDialog, ImageView, MyBatch },

  computed: {
    items() {
      return this.$store.getters["intent_template/list"];
    },
  },

  data() {
    return {
      displayEditDialog: false,
      theEditDialogItem: {},
      selectedItems: [],
    };
  },

  methods: {
    handleClickItemEdit(item) {
      this.theEditDialogItem = item;
      this.displayEditDialog = true;
    },

    handleClickItemRemove(item) {
      let title = "删除命令模板";
      let text = "确定要删除 [" + item.name + "] 这个命令模板吗？";
      let desc = "";
      ElMessageBox.confirm(text + desc, title, {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          this.removeItem(item);
        })
        .catch(() => {});
    },

    removeItem(item) {
      let id = item.id;
      this.$store.dispatch("intent_template/remove", { id });
    },

    handleAction(e) {
      this.$emit("action", e);
    },

    handleSelectionChange(items) {
      this.selectedItems = items;
    },
  },
};
</script>
