<template>
  <div class="toolbar">
    <el-button type="default" @click="fetch" title="刷新">
      <el-icon> <Refresh /> </el-icon>
    </el-button>
    <el-button type="default" @click="insert" title="添加">
      <el-icon><Plus /></el-icon>
    </el-button>

    <el-dropdown style="margin-left: 15px" @command="handleCommand">
      <el-button type="default" title="更多">
        <el-icon><More /></el-icon>
      </el-button>
      <template #dropdown>
        <el-dropdown-menu>
          <el-dropdown-item command="IMPORT_PRESET_TEMPLATES">
            导入预置模板
          </el-dropdown-item>
        </el-dropdown-menu>
      </template>
    </el-dropdown>
  </div>
</template>

<script setup>
import { Refresh, Plus, More } from "@element-plus/icons-vue";
</script>

<script>
export default {
  name: "settings-intent-templates-toolbar",

  components: {},

  data() {
    return {};
  },

  methods: {
    fetch() {
      this.$store.dispatch("intent_template/fetch");
    },

    insert() {
      this.fireAction("insert");
    },

    fireAction(name) {
      let e = { name };
      this.$emit("action", e);
    },

    handleCommand(cmd) {
      if (cmd == "IMPORT_PRESET_TEMPLATES") {
        this.doImportPresetTemplates();
      }
    },

    doImportPresetTemplates() {
      this.$store.dispatch("intent_template/import_preset_templates", {});
    },
  },

  mounted() {
    this.fetch();
  },
};
</script>

<style scoped>
.toolbar {
  text-align: right;
  margin-top: 50px;
  margin-bottom: 20px;
}
</style>
