<template>
  <div class="index-page">
    <h1>命令模板</h1>

    <MyToolbar @action="handleAction" />

    <MySearch class="search-box" />
    <MyPager class="pager-box" />

    <MyTable @action="handleAction" />

    <MyEditDialog
      class="insert-dialog"
      v-model="displayInsertDialog"
      method="POST"
      @action="handleAction"
    />

    <MyMacroListDialog v-model="displayMacroListDialog" />
  </div>
</template>

<script>
import MyToolbar from "./toolbar.vue";
import MyTable from "./table.vue";
import MyPager from "./pager.vue";
import MySearch from "./search.vue";
import MyEditDialog from "./edit-dialog.vue";
import MyMacroListDialog from "./macro-list-dialog.vue";

export default {
  name: "settings-intent-templates-index",

  components: {
    MyToolbar,
    MyTable,
    MyPager,
    MyEditDialog,
    MyMacroListDialog,
    MySearch,
  },

  data() {
    return {
      displayInsertDialog: false,
      displayMacroListDialog: false,
    };
  },

  methods: {
    handleAction(e) {
      let name = e.name;
      if (name == "insert") {
        this.insert();
      } else if (name == "list:macro") {
        this.displayMacroListDialog = true;
      }
    },

    insert() {
      this.displayInsertDialog = true;
    },
  },
};
</script>

<style scoped>
.index-page {
  text-align: left;
}

.search-box {
  max-width: 380px;
}

.pager-box {
  margin-top: 50px;
  margin-bottom: 20px;
}
</style>
