<template>
  <div>
    <el-input
      :modelValue="theModelValue"
      @update:modelValue="handleUpdateModelValue"
      placeholder="输入要查找的内容"
    >
      <template #append>
        <el-button title="查找">
          <el-icon> <Search /> </el-icon>
        </el-button>
      </template>
    </el-input>
  </div>
</template>

<script setup>
import { Search } from "@element-plus/icons-vue";
</script>

<script>
export default {
  name: "settings-intent-templates-search",

  computed: {
    theModelValue() {
      return this.$store.getters["intent_template/filter"];
    },
  },

  methods: {
    handleUpdateModelValue(value) {
      let filter = value;
      this.$store.commit("intent_template/update", { filter });
    },
  },
};
</script>
