<template>
  <div>
    <el-select :model-value="modelValue" @update:modelValue="fireUpdateModelValue">
      <el-option v-for="item in options" :key="item.id" :label="item.title" :value="item.urn">
        <div class="select-option-box">
          <span>
            <img class="select-option-icon-image" :src="item.icon" />
          </span>
          <span> {{ item.name }} </span>
        </div>
      </el-option>
    </el-select>

    <el-button @click="refresh">
      <el-icon>
        <Refresh />
      </el-icon>
    </el-button>
  </div>
</template>

<script setup>
import { Refresh } from "@element-plus/icons-vue";
</script>

<script>
export default {
  name: "executable-urn-selector",

  computed: {
    options() {
      return this.$store.getters["executable/all"];
    },
  },

  methods: {
    fireUpdateModelValue(value) {
      this.$emit("update:modelValue", value);
    },

    refresh() {
      this.$store.dispatch("executable/fetch");
    },
  },

  props: {
    modelValue: String,
  },
};
</script>

<style scoped>
.select-option-icon-image {
  max-width: 23px;
  max-height: 23px;
  margin-right: 10px;
}

.select-option-box {
  display: flex;
  justify-items: flex-start;
  align-items: flex-start;
  flex-direction: row;
}
</style>
